@import url(https://fonts.googleapis.com/css?family=Archivo);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




body{
  background-color: #65CCB8 !important;
  color: #F2F2F2;
}

.App {
  text-align: center;
  height: 100vh; 
}

.header{
  font-family: 'Archivo';
  font-weight: bold;
  font-size: 40px;

}

.mb-3{
  width: 40%;
  margin: auto;
}

@media(max-width: 1024px){
  .mb-3{
    width: 80%;
  }
}

@media(max-width: 414px){
  .mb-3{
    width: 90%;
  }
}

.weatherCardContainer{
  max-width: 80%;
  position: relative;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-height: 355px;
}

.weatherCard{
    color: #F2F2F2;
    width: 350px;
    border: 1px solid white;
    padding: 0px 5px 0px 5px;
    font-size: 32px;
}

.weatherCard > .dayName{
    padding-top: 10px;
    min-height: 14%;
    overflow-x: hidden;
}

.weatherCard > .iconContainer{
    height: 40%;
    line-height: 70.8px;
}

.description{
    margin-top: 25px;
    font-size: 25px;
}

.iconContainer > i{
   font-size: 130px;
   padding-top: 15px;
}

.weatherCard > .temperature{
    word-break: break-all;
    height: 15%;
    margin-top: 20px;
    display: flex;
    font-size: 36px;
    flex-direction: row;
    justify-content: space-between;
}

